<template>
  <div class="page-list">
    <el-tag
      size="small"
      class="m-b-4 m-r-4"
      v-for="(item, idx) in status ? tag : halfTag"
      :key="`${idx}`"
      @click="open(item)"
      >{{ item }}</el-tag
    >
    <el-button size="mini" type="primary" @click="status = !status">{{
      status ? "收起" : "展开"
    }}</el-button>
    <el-result v-if="formInline.total === 0" icon="warning" title="暂无数据">
      <template slot="extra">
        <el-button type="primary" @click="$router.back()" size="medium"
          >返回</el-button
        >
      </template>
    </el-result>
    <div v-else class="center-center m-b-8" v-loading="loading">
      <el-pagination
        @current-change="queryApplyInfo"
        :current-page.sync="formInline.pageNumber"
        :page-size="formInline.pageSize"
        background
        layout="pager"
        :pager-count="5"
        :total="formInline.total"
      />
    </div>
    <LayoutList :docs="list.docs" />
    <div class="center-center" v-loading="loading">
      <el-pagination
        @current-change="queryApplyInfo"
        :current-page.sync="formInline.pageNumber"
        :page-size="formInline.pageSize"
        background
        layout="pager"
        :pager-count="5"
        :total="formInline.total"
      />
    </div>
  </div>
</template>

<script>
import apiUser from "@/api/user";
import tag from "@/plugins/tag.json";
export default {
  name: "HomeView",
  data() {
    return {
      tag,
      show: false,
      path: this.$route.fullPath,
      loading: false,
      formInline: {
        pageNumber: 1,
        roleName: "",
        total: null,
        pageSize: 60,
        sysType: "MGR",
      },
      list: {
        docs: [],
      },
      status: false,
    };
  },
  computed: {
    halfTag() {
      return this.tag.filter((item, idx) => idx < 20);
    },
  },
  methods: {
    open(item) {
      window.location.href = `/cat/${item}`;
    },
    async queryApplyInfo() {
      //this.list.nextPage
      const page = this.formInline.pageNumber || 1;
      this.loading = true;
      const [err, res] = await apiUser.queryApplyInfo(this.path, {
        page,
      });
      this.loading = false;
      if (err) return;
      this.formInline.total = res.totalDocs;
      this.formInline.pageSize = res.limit;
      //totalDocs
      let docs = res.docs;
      // if (page === 1) {
      //   docs = [...this.list.docs, ...res.docs];
      // }
      this.list = {
        ...res,
        docs,
      };
    },
  },
  created() {
    this.queryApplyInfo();
  },
  mounted() {
    this.show = true;
  },
};
</script>
<style scoped lang="scss">
.page-list {
  ::v-deep {
    .el-pager > li:last-child {
      display: none;
    }
  }
}
</style>
